/* App.css */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  list-style-type: none;
}

header {
  background-color: hsla(0, 0%, 94%, 0.212);
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

}

header nav {
  margin-top: -50px;
}

header img {
  height: 70px;
  margin-left: -400px;
}

nav ul {
  list-style-type: none;
  display: flex;
}

nav ul li {
  margin-right: 20px;
  cursor: pointer;
}

main {
  padding: 20px 0;
}

footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 20px 0;
}
